import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { HeroService }  from '../hero.service';

@Component({
  selector: 'app-reset',
  templateUrl: './reset.component.html',
  styleUrls: [ './reset.component.css' ]
})
export class ResetComponent implements OnInit {
  message : any = {};
  error : any = {};
  password1: string = '';
  password2: string = '';
  token: string = '';
  constructor(
    private route: ActivatedRoute,
    private heroService: HeroService
  ) {}

  ngOnInit(): void {
    this.getToken();
  }

  getToken(): void {
    this.token = this.route.snapshot.paramMap.get('token');
    console.log(this.token);
  }

  reset() {
    var form = {
      password1: this.password1,
      password2: this.password2,
      token: this.token
    };
    this.error = {};
    if(this.password1.length === 0) {
      this.error.message = 'Por favor, introduce una contraseña.';
    } else {
      if (this.password1 === this.password2) {
        this.heroService.resetPassword(form).subscribe(response => {
          this.message = response;
        },
        error => {
          this.error.message = 'Se ha producido un error inesperado.'
        })
      } else {
        this.error.message = 'Las contraseñas no coinciden.'
      }
    }
  }
}



