import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { DashboardComponent }   from './dashboard/dashboard.component';
import { HelpComponent }   from './help/help.component';
import { ConditionsComponent }   from './conditions/conditions.component';
import { PrivacyComponent }   from './privacy/privacy.component';
import { PrivacidadComponent }   from './privacidad/privacidad.component';
import { HeroDetailComponent }   from './hero-detail/hero-detail.component';
import { FastpassComponent }   from './fastpass/fastpass.component';
import { GetAppComponent }   from './get-app/get-app.component';
import { EulaComponent } from './eula/eula.component';
import { AuthComponent } from './auth/auth.component';
import { RecoverComponent } from './recover/recover.component';
import { ResetComponent } from './reset/reset.component';
import { ShortcutsComponent } from './shortcuts/shortcuts.component';

const routes: Routes = [
  { path: '', redirectTo: '/dashboard', pathMatch: 'full' },
  { path: 'help', component: HelpComponent },
  { path: 'conditions', component: ConditionsComponent },
  { path: 'terms', component: ConditionsComponent },
  { path: 'fastpass', component: FastpassComponent },
  { path: 'privacy', component: PrivacyComponent },
  { path: 'privacidad', component: PrivacidadComponent },
  { path: 'product/:id', component: HeroDetailComponent },
  { path: 'reset/:token', component: ResetComponent },
  { path: 'app', component: GetAppComponent },
  { path: 'dashboard', component: DashboardComponent },
  { path: 'eula', component: EulaComponent },
  { path: 'auth', component: AuthComponent },
  { path: 'recover', component: RecoverComponent },
  { path: 'shortcuts', component: ShortcutsComponent },
  { path: '**', component: DashboardComponent }
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}



