import { Component, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';

@Component({
  selector: 'shortcuts',
  templateUrl: './shortcuts.component.html',
  styleUrls: [ './shortcuts.component.css' ]
})
export class ShortcutsComponent implements OnInit {

  constructor(
    public meta: Meta,
    public title: Title) {

    }

  ngOnInit() {
    let title = 'Shortcuts para utilizar con Salva';
    let description = 'Funciones rápidas relacionadas con Salva y disponibles en iOS 12.';
    this.meta.updateTag({ name: 'description', content: description });
    this.meta.updateTag({ property:"og:image", content:"./assets/img/shortcuts.png" });
    this.meta.updateTag({ property:"og:url", content:"https://www.getsalva.com/shortcuts" });
    this.meta.updateTag({ property:"og:title", content: title });
    this.meta.updateTag({ property:"og:description", content: description });
    this.title.setTitle(title);
  }
}



